import React from 'react';
import styled from 'styled-components';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';
import Fade from 'react-reveal/Fade';

import { Container } from '@components/global';
import Call2Act from '@ui/Call2Act';

const Header = () => (
  <StaticQuery
    query={graphql`
      query {
        masthead: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "masthead_product" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1400) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    `}
    render={data => (
      <HeaderWrapper>
        <Container>
          <Grid>
            <Fade>
            <Text>
              <h1>
              Hearing technology for everyone
              </h1>
              <br/>
              <p>
              We are happy to introduce Hai, the first earphone that is universal for hearing aid users, cochlear implant users, and normal hearing people.<br/> 
              </p>
              <Call2Act text="Visit our campaign" href="https://www.indiegogo.com" target="_blank"/>
            </Text>
            </Fade>
          </Grid>
          <Art>
            <Img fluid={data.masthead.childImageSharp.fluid} />
          </Art>
        </Container>
      </HeaderWrapper>
    )}
  />
);

const HeaderWrapper = styled.header`
  background-color: ${props => props.theme.color.primary};
  padding-top: 36px;
  height: 100vh;
  overflow: hidden;

  @media (max-width: ${props => props.theme.screen.md}) {
    padding-top: 128px;
  }
`;

const Art = styled.div`
  width: 100%;
  z-index: 1;
  top: 0;
  left: 0;
  height: 100vh;
  position: absolute;
  overflow: hidden;
  @media (max-width: ${props => props.theme.screen.md}) {
    &:after {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: linear-gradient(to bottom, rgba(255,245,238, 1) 50%, rgba(255,245,238,0.3)); 
      transition: all .3s linear;
    }
  }

  > div {
    width: 100%;
    margin-bottom: -14.5%;

    @media (max-width: ${props => props.theme.screen.md}) {
      height: 100%;
      width: auto;
      margin-top: 20%;
    }
    @media (max-width: ${props => props.theme.screen.sm}) {
      margin-top: 55%;
    } 
  }
`;

const Grid = styled.div`
  display: grid;
  grid-template-columns: 3fr 5fr;
  align-items: center;
  grid-gap: 75px;
  z-index: 2;
  top: 17vh;
  left: 0;
  position: relative;
  @media (max-width: ${props => props.theme.screen.lg}) {
    top: 15vh;
  }

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    grid-gap: 80px;
    top: 10vh;

    > ${Art} {
      order: 1;
    }
  }

  @media (max-width: ${props => props.theme.screen.sm}) {
    top: 0vh;
  }
`;

const Text = styled.div`
  justify-self: start;
  @media (max-width: ${props => props.theme.screen.md}) {
    justify-self: start;
  }
`;


export default Header;
