import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';

import { Section} from '@components/global';
import SectionHeader from '@ui/sectionheader';
import ImgTxt from '@ui/imgtxt';
import ImgTxtInverse from '@ui/imgtxt_inverse';
import ImgSingle from '@ui/img_single';
//import Video from '@ui/video';


const Intro = () => (
  <StaticQuery
    query={graphql`
      query {
        Earphone4all: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "earphone4all" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Unidesign: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "unidesign" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Music: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "music" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Noise: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "noise" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Mobileeq: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "mobileeq" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Ui: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "ui" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Conversation: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "conversation" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Stickers: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "stickers" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
        Anywhere: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "anywhere" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
      }
    `}
    render={data => (
      <Section id="intro">
        <SectionHeader 
          header="A problem happening to many hearing aids and cochlear implant users" 
          subheader="It’s not likely that any styles of earbuds and earphones are going to work with hearing aids. The reason is quite straightforward: hearing aids already sit within ears, so fitting another device into the ears doesn't work."
        />
        {/*<Video
          header="The first earphone for all hearing conditions" 
          text="We are launching a campaign for producing a brand new earphone design for you — no matter you are normal hearing, a hearing aid user, or a cochlear implant user."
          video="398411002"
          togglecover="false"  
          toggletop="true"       
        />*/}
        <ImgSingle
          header="The first earphone for all hearing conditions" 
          text="We are launching a campaign for producing a brand new earphone design for you — no matter you are normal hearing, a hearing aid user, or a cochlear implant user."
          video="398411002"
          togglecover="false"  
          toggletop="true"      
        >
          <Img fluid={data.Earphone4all.childImageSharp.fluid} />
        </ImgSingle>
        <ImgTxtInverse 
          header="Universal design" 
          text="Rearia Hai is the first earphone that is universal for hearing aid users, cochlear implant users, and normal hearing people. Thanks to its modularized design with telecoil technology. You can wear it no matter whether you wear bilateral aids, single-sided, or just no aids at all. You can quickly and easily switch between the earbuds and coils and mix them as you want." 
        >
          <Img fluid={data.Unidesign.childImageSharp.fluid} />
        </ImgTxtInverse>
        <ImgTxt 
          header="Superior sound quality" 
          text="Telecoil technology is an old technology which provides high sound quality and is compatible with most of the hearing aid and cochlear implant products. We used this technology as basis and with our HiFi specialized team developed an outstanding sound quality for enjoying music"
        >
          <Img fluid={data.Music.childImageSharp.fluid} />
        </ImgTxt>
        <ImgTxtInverse 
          header="Dynamic Buzz Cancellation" 
          text="To even further improve listening experience, we integrated Dynamic Buzz Cancellation to block any unwanted interference from electrical infrastructure. This allows the listener to hear great sound quality even in difficult environments, such as in a plane or near high-voltage systems." 
        >
          <Img fluid={data.Noise.childImageSharp.fluid} />
        </ImgTxtInverse> 
        <ImgSingle
          header="Adjustable equalizer" 
          text="With Hai App, we provide even more! Change your preferred equalizer settings and save them to suit all listening situations. You can also change the stereo balance to fit Hai with your hearing condition. Connect with others, you can also share your hearing profile with other users similar to you." 
          togglecover="true"  
          toggletop="false"       
        >
           <Img fluid={data.Mobileeq.childImageSharp.fluid} />
        </ImgSingle>
        <ImgTxt
          header="Intuitive and seamless usage" 
          text="Convenient design allows for easy usage with three buttons operation; answer calls, change tracks and adjust volume easily. You can also activate your smartphone assistant, seamless integration with your smartphone."    
        >
          <Img fluid={data.Ui.childImageSharp.fluid} />
        </ImgTxt>
        <ImgTxtInverse 
          header="Clear conversations" 
          text="Call to your loved ones with the built-in microphone which provides clear voice and low latency." 
        >
          <Img fluid={data.Conversation.childImageSharp.fluid} />
        </ImgTxtInverse>
        <ImgSingle
          header="Express your style" 
          text="Choose either a black or white device and customize your product with different coloured surfaces and stickers! Let your creativity run free and let your device express your style."
          togglecover="true"  
          toggletop="false"       
        >
          <Img fluid={data.Stickers.childImageSharp.fluid} />
        </ImgSingle>
        <ImgTxt
          header="With you anywhere, anytime" 
          text="Whether the sun is shining, or its raining outside, you can bring Hai anywhere with you. Its lightweight design allows it to easily clip to your clothes, or wear it like a necklace! Water resistant and modern design lets you wear it while exercising, working or just relaxing and enjoying the music"  
        >
          <Img fluid={data.Anywhere.childImageSharp.fluid} />
        </ImgTxt>  
      </Section>
    )}
  />
);

export default Intro;
